
import React, { Component, PropTypes } from 'react';
import DateItem from './DateItem';
import timing from 'utils/date';

import './Calendar.less';
const monthCaptions = ["", "", "", "", "", "", "", "", "", "", "", ""];
const weekCaptions = ['','','','','','',''];
const dateOfWeekCount = 7;
const weekCount = 6;

class Calendar extends Component {

    static propsTypes = {
        onPreviousMonth: PropTypes.func,
        onNextMonth: PropTypes.func,
        onDateClick: PropTypes.func,
        onClear: PropTypes.func,
        date: PropTypes.object,
        month: PropTypes.object,
        today: PropTypes.object,
        contentMarks: PropTypes.arrayOf(PropTypes.number)
    };

    constructor() {
        super(...arguments);
        this.handleDateClick = this.handleDateClick.bind(this);
        this.handlePreviousMonth = this.handlePreviousMonth.bind(this);
        this.handleNextMonth = this.handleNextMonth.bind(this);
    }

    getMonthDates (monthDate) {
        var month     = monthDate.getMonth(),
            year      = monthDate.getFullYear(),
            firstDate = new Date(year, month, 1, 0, 0, 0),
            day       = firstDate.getDay(),
            dates     = [],
            currentDate;

        if (day === 0) {
            day = 7;
        }
        firstDate = new Date(year, month, -day + 2, 1);
        currentDate = firstDate;
        for (let i = 0; i < this.weekCount * this.dateOfWeek; i += 1) {
            dates.push(new Date(currentDate.getTime()));
            currentDate = new Date(currentDate.getFullYear(), currentDate.getMonth(), currentDate.getDate() + 1, 0, 0, 0);
        }
        return dates;
    }

    handlePreviousMonth() {
        this.props.onPreviousMonth();
    }

    handleNextMonth() {
        this.props.onNextMonth();
    }

    handleDateClick(event, date) {
        this.props.onDateClick(date);
    }

    render () {
        var { month: currentMonth, date: currentDate, today, contentMarks } = this.props,
            nodes = [],
            dates,
            monthNumber,
            year;

        if (currentMonth) {
            monthNumber = currentMonth.getMonth();
            year = currentMonth.getFullYear();
            dates = this.getMonthDates(currentMonth);
            nodes = dates.map((date, index) => {
                var dateString = timing.toDateString(date),
                    isOtherMonth = !timing.isEqualMonths(date, currentMonth),
                    isCurrentDate = !!currentDate && dateString === currentDate,
                    isToday = dateString === today,
                    isThereContent = !!contentMarks && !!~contentMarks.indexOf(dateString);
                return (
                    <DateItem key={index}
                              date={date}
                              isCurrentDate={isCurrentDate}
                              isToday={isToday}
                              isThereContent={isThereContent}
                              onClick={(event) => {
                                this.handleDateClick(event, dateString);
                              }}
                              isOtherMonth={isOtherMonth} />
                );
            });
        }

        return (
            <table className="calendar table-bordered table-hover">
                <thead>
                    <tr>
                        <th colspan="7">
                            <span className="pull-left">
                                <div className="arrow-left"></div>
                            </span>
                            <span className="center-block"> 2016</span>
                            <span className="pull-right">
                                <div className="arrow-right"></div>
                            </span>
                        </th>
                    </tr>
                    <tr>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                        <th>
                            
                        </th>
                    </tr>
                </thead>
                <tr>
                    <td className="calendar__date-item calendar__date-item_other-month">31</td>
                    <td className="calendar__date-item">1</td>
                    <td className="calendar__date-item">2</td>
                    <td className="calendar__date-item">3</td>
                    <td className="calendar__date-item">4</td>
                    <td className="calendar__date-item">5</td>
                    <td className="calendar__date-item">6</td>
                </tr>
                <tr>
                    <td className="calendar__date-item">7</td>
                    <td className="calendar__date-item">8</td>
                    <td className="calendar__date-item">9</td>
                    <td className="calendar__date-item">10</td>
                    <td className="calendar__date-item">11</td>
                    <td className="calendar__date-item">12</td>
                    <td className="calendar__date-item">13</td>
                </tr>
                <tr>
                    <td className="calendar__date-item">14</td>
                    <td className="calendar__date-item">15</td>
                    <td className="calendar__date-item">16</td>
                    <td className="calendar__date-item">17</td>
                    <td className="calendar__date-item">18</td>
                    <td className="calendar__date-item active">19</td>
                    <td className="calendar__date-item">20</td>
                </tr>
                <tr>
                    <td className="calendar__date-item">21</td>
                    <td className="calendar__date-item">22</td>
                    <td className="calendar__date-item">23</td>
                    <td className="calendar__date-item">24</td>
                    <td className="calendar__date-item">25</td>
                    <td className="calendar__date-item">26</td>
                    <td className="calendar__date-item">27</td>
                </tr>
                <tr>
                    <td className="calendar__date-item">28</td>
                    <td className="calendar__date-item">29</td>
                    <td className="calendar__date-item">30</td>
                    <td className="calendar__date-item calendar__date-item_other-month">1</td>
                    <td className="calendar__date-item calendar__date-item_other-month">2</td>
                    <td className="calendar__date-item calendar__date-item_other-month">3</td>
                    <td className="calendar__date-item calendar__date-item_other-month">4</td>
                </tr>
            </table>
        );
    }

}

export default Calendar;


