
import React, { Component, PropTypes } from 'react';
import { Calendar } from 'components'

class App extends Component {

    render() {
        var contentMarks = [],
            month = new Date();
        return (
            <div>
                <Calendar month={month}
                          date={month}
                          today={month}
                          contentMarks={[]} />
            </div>
        );
    }
}

export default App;



